  /* border-radius: 0; */ /* Убираем обнуление */
  border-radius: 4px; /* Возвращаем стандартное скругление */
  font-size: 1rem;
  color: #212529; /* <--- Явно задаем цвет текста */
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  width: 100%;
